<template>
  <div>
    <div class="d-block header-section">
      <h1>
         <b>Manufacturers</b>
      </h1>
      <b-col class="mb-2 col-md-4 float-right search-section">
          <b-form-group label="" label-cols-sm="3" label-align-sm="right" label-size="sm" label-for="filterInput" class="mb-0">
            <b-input-group>
            <b-form-input v-model="filter" class="form-control" type="search" id="filterInput" placeholder="Type to Search">nkjk</b-form-input>
            
            </b-input-group>
          </b-form-group>
      </b-col>
    </div>
    <div class="container-fluid order-list">
      <!-- START: Card Data-->
      <div class="row">
        <div class="col-12 col-md-12 mt-3 mb-5">
          <div class="card pb-3">
            <div class="list-filter">
              <div class="btn-group">
                <input type="radio" id="All" name="radios" autocomplete="off" class="custom-control-input" checked :value="AllText">
                <label for="All" type="button" class="btn btn-outline-dark rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('All')">{{AllText}}</label>
              </div>
              <div class="btn-group">
                <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Submitted" :value="SubmittedText">
                <label type="button" class="btn btn-outline-info rounded-btn mr-2 mob-mar" for="Submitted" @click="onStatusFilterClicked('Submitted')">{{SubmittedText}}</label>
              </div>
              <div class="btn-group">
                <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Active" :value="ActiveText">
                <label type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" for="Active" @click="onStatusFilterClicked('Active')">
                {{ActiveText}}</label>
              </div>
              <div class="btn-group">
                <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="InActive" :value="InActiveText">
                <label type="button" class="btn btn-outline-primary rounded-btn mr-2 mob-mar" for="InActive" @click="onStatusFilterClicked('InActive')">
                {{InActiveText}}</label>
              </div>

              <div class="btn-group">
                <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Imported" :value="ImportedText">
                <label type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" for="Imported" @click="onStatusFilterClicked('Imported')">
                {{ImportedText}}</label>
              </div>
              <div class="btn-group">
                <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Domestic" :value="DomesticText">
                <label type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" for="Domestic" @click="onStatusFilterClicked('Domestic')">
                {{DomesticText}}</label>
              </div>
            </div>
            <div class="overflow-auto">
			<b-table
				id="mfList"
				:items="this.getManufacturers"
				:filter="filter"
				:filterIncludedFields="filterOn"
				filter-debounce="1000"
				:fields="fields"
				:current-page="currentPage"
				:per-page="perPage"
				:sort-by.sync="sortBy"
				:sort-desc.sync="sortDesc"
				sort-icon-left          
				class="table order">
				<template v-slot:cell(status)="data">
				<span :class="loadStatusClass(data.item.status)">{{data.item.status}}</span>
				</template>
				<template v-slot:cell(action)="data">
        <router-link v-if="data.item.status == 'Submitted' || data.item.status == 'Resubmitted'" :to="{ name: 'ReviewMf', params: {mfId: data.item.mfId} }" >Review</router-link>
        <router-link v-else :to="{ name: 'ViewMf', params: {mfId: data.item.mfId} }" >View</router-link>
      </template>
			</b-table>
			<b-pagination
                v-model="currentPage"
                :total-rows="this.totalRows"
                :per-page="perPage"
                class="float-right mr-4"
                first-text="First"
                prev-text="Prev"
                next-text="Next"
                last-text="Last"
              ></b-pagination>
              
            </div>
          </div>
          <!-- START: Card Data-->
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data(){
    return {
       filter: null,
       filterOn: [],
       statusFilter:"All",
       AllText:'',
       SubmittedText:'',
       ActiveText:'',
       InActiveText:'',
       DomesticText:'',
       ImportedText:'',
       totalRows: 1,
       currentPage: 1,
       perPage: 10,
       pageOptions: [5, 10, 15],
       sortBy: 'actionTakenOn',
       sortDesc: true,
       sortDirection: 'desc',
       fields: [
          {
            key: 'name',
            label:'Name',
            sortable: true
          },
          {
            key: 'origin',
            label:'Origin',
            sortable: false,
          },
          {
            key: 'tinNumber',
            lable:'TIN Number',
            sortable: true
          },
          {
            key: 'actionTakenOn',
            label: 'Action Taken On',
            sortable: true,
          },
          {
            key: 'status',
            label: 'Status',
            sortable: false,
          },
          {
            key:'action',
            lable: 'Action',
            sortable:false,
            
          }
        ]

    }
  },
  methods: {
    getManufacturers(ctx){
      const promise = axios({
        url: '/Manufacturer/GetManufacturers',
        method:'post',
        baseURL: process.env.VUE_APP_VmApisBaseUrl,
        data: {PageSize:ctx.perPage,PageNumber:ctx.currentPage,SearchByParameter:ctx.filter,SearchByStatus:this.statusFilter,SortBy:ctx.sortBy,SortByDesc:ctx.sortDesc},
      })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        // Pluck the array of items off our axios response
        console.log(response)
        const items = response.data.miniMfsDtos
        this.totalRows = response.data.totalCount;
        this.AllText = this.getStatusButtonText("All", response.data.totalCount);
        this.SubmittedText = this.getStatusButtonText("Submitted", response.data.submittedCount);
        this.ActiveText = this.getStatusButtonText("Active", response.data.activeCount);
        this.InActiveText = this.getStatusButtonText("InActive", response.data.inActiveCount);
        this.DomesticText = this.getStatusButtonText("Domestic", response.data.domesticCount);
        this.ImportedText = this.getStatusButtonText("Imported", response.data.importedCount);
        // Must return an array of items or an empty array if an error occurred
        return items || []
  })
    },
  onStatusFilterClicked(status){
    this.statusFilter = status;
    //force refresh on status change
    this.$root.$emit('bv::refresh::table', 'mfList');
  },
  getStatusButtonText(buttonText, count)
  {
    var text = (this.statusFilter == "All" || this.statusFilter == buttonText)? buttonText + '(' + count + ')': buttonText;
    return text;
  },
  loadStatusClass(status){
    switch (status) {
      case "Active":
        return "text-success";
      case "InActive":
        return "text-secondary";
      case "Submitted":
        return "text-primary";
      case "Domestic":
        return "text-warning";
      case "Imported":
        return "text-dark";
      case "Rejected":
        return "text-danger";
      case "Approved":
        return "text-info";
      case "Resubmitted":
        return "text-primary";
      default:
        return "text-primary";
    }
  }
}
}
</script>
